* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
header {
  width: 100%;
  height: 70px;
  background-color: royalblue;
  color: white;
}
h1 {
  line-height: 70px;
  padding-left: 2rem;
}
.desc_wrap {
  padding: 1rem 2rem;
  border-bottom: 1px solid #000;
}
button {
  margin: 2rem 1rem 0;
  padding: 0.5rem 1rem;
  cursor: pointer;
}
.thumbsUp {
  float: right;
  cursor: pointer;
  width: 60px;
  height: 60px;
  padding: 0.3rem;
  text-align: center;
  margin-top: -0.5rem;
  border: 1px solid #000;
  border-radius: 10px;
}
#modal {
  display: none;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(2, 7, 21, 0.5);
  backdrop-filter: blur(3px);
}
#modal .modal_desc {
  padding: 2rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50vw;
  height: 50vw;
  text-align: center;
  background-color: #fff;
}
#modal .modal_desc h3 {
  margin-bottom: 2rem;
}
#modal.on {
  display: block;
}
.modal {
  position: absolute;
  left: 50%;
  top: 50%;
  background-color: azure;
  border: 1px solid #000;
  transform: translate(-50%, -50%);
  width: 500px;
  height: 500px;
  text-align: center;
  line-height: 500px;
}
